* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 20px;
  font-family: Roboto Slab;
  font-size: medium;
}

h1 {
  text-align: center;
}

.search-form {
  text-align: center;
  margin-bottom: 20px;
}

.gallery {
  grid-gap: 20px;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  display: grid;
}

.photo-card {
  background-color: #f4f4f4;
  border-radius: 5px;
  padding: 10px;
  box-shadow: 0 2px 4px rgba(0, 0, 0, .1);
}

.photo-card img {
  width: 100%;
  height: auto;
  border-radius: 5px;
  margin-bottom: 10px;
}

.info {
  margin-top: 10px;
}

.info-item {
  margin-bottom: 5px;
  font-weight: bold;
}

.notiflix-notify {
  color: #fff !important;
  background-color: #333 !important;
}

.notiflix-notify.failure {
  background-color: #f44 !important;
}

